.button-to-top {
    display: none;
    position: fixed;
    bottom: 15px;
    right: 20px;
    padding: 10px;
    border-radius: 10px;
    z-index: 9;
    border: none;
    background-color: $accent-color;
    color: white;
    cursor: pointer;
    
    font-family: $dm-sans;
    font-size: 14px;
    font-weight: 700;

    opacity: 0.4;
    transition: opacity 500ms cubic-bezier(0.4, 0, 0.2, 1),
    border-radius 500ms cubic-bezier(0.4, 0, 0.2, 1);

    @include mq(tablet) {
        bottom: 35px;
        right: 30px;
        font-size: 24px;
        padding: 15px;

    }
}

.button-to-top:hover {
    opacity: 1;
    border-radius: 50%;
}