.customers__modal {
  position: absolute;
  left: 50%;
  top: 50%;
  margin: 0 auto;
  padding: 30px;
  border-radius: 5px;
  max-width: 450px;
  width: 95vw;
  max-height: 100vh;
  overflow: scroll;
  background-color: $primary-bg-color;
  box-shadow: $contacts-shadow;
  transition: transform $main-effect;
  transform: translate(-50%, -50%) scale(1);

  @include maxMedia($min) {
    position: fixed;
    min-width: 320px;
  }
  @include minMedia($tab) {
    border-radius: 10px;
    padding: 40px;
    overflow: auto;
    height: auto;
    // max-width: 640px;
  }
}

.modal-close-btn {
  position: absolute;
  right: 8px;
  top: 8px;
  width: 30px;
  height: 30px;
  stroke: $primary-color;
  background-color: inherit;
}

.modal__title.section-subtitle {
  text-align: center;
  font-family: 'Titan One';
  position: relative;
  color: $accent-color;
  font-size: 26px;
  padding-bottom: 160px;
  margin-bottom: 0;

  @include mq(tablet) {
    font-size: 36px;
    padding-bottom: 230px;
  }
}

.modal--ice-cream .modal__title::after {
  content: '';
  width: 150px;
  height: 150px;

  position: absolute;
  display: block;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -40%);
  background-image: url('../images/products/mobile/ice-cream-mobile-x1.png');
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  border-radius: 50%;

  @media screen and (min-device-pixel-ratio: 2),
    screen and (min-resolution: 192dpi),
    screen and (min-resolution: 2dppx) {
    background-image: url('../images/products/mobile/ice-cream-mobile-x2.png');
  }

  @include mq(mobile) {
    background-image: url('../images/products/tablet/ice-cream-tablet-x1.png');

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url('../images/products/tablet/ice-cream-tablet-x2.png');
    }
  }

  @include mq(tablet) {
    width: 220px;
    height: 226px;
    transform: translate(-50%, -50%);
    background-image: url('../images/products/tablet/ice-cream-tablet-x1.png');

    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url('../images/products/tablet/ice-cream-tablet-x2.png');
    }
  }

  @include mq(desktop) {
    width: 220px;
    height: 220px;
    background-image: url('../images/products/ice-cream-desktop-x1.png');
    @media screen and (min-device-pixel-ratio: 2),
      screen and (min-resolution: 192dpi),
      screen and (min-resolution: 2dppx) {
      background-image: url('../images/products/ice-cream-desktop-x2.png');
    }
  }
}

