.modal__text {
  margin-bottom: 30px;
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  text-align: center;
  letter-spacing: 0.04em;
  color: $secondary-text-color;

  @include mq(mobile) {
    margin-bottom: 50px;
    font-size: 16px;
  }

  @include mq(tablet) {
    line-height: 1.79;
    margin-bottom: 20px;
  }
  @include mq(desktop) {
    font-size: 18px;
    line-height: 1.75;
    margin-bottom: 10px;
  }
}
.modal__order-link {
  width: 80px;
  height: 40px;
  margin: 0 auto;
  padding: 10px;
  border-radius: 20px;
  font-size: 16px;
}

.btn-dark {
